@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
  -webkit-align-items: center;
          align-items: center;
  background-image: linear-gradient(to right, transparent, silver, silver, transparent);
  display: -webkit-flex;
  display: flex;
  font-family: "Permanent Marker", cursive;
  font-size: 1.8rem;
  -webkit-justify-content: center;
          justify-content: center;
}

.todd-img {
  height: 70px;
  position: relative;
  top: 12px;
}

@media screen and (max-width: 760px) {
  .Header {
    font-size: 1.2rem;
  }

  .todd-img {
    height: 60px;
    position: relative;
    top: 12px;
  }
}

@media screen and (max-width: 500px) {
  .Header {
    color: rgb(238, 238, 238);
    font-size: 0.8rem;
  }

  .todd-img {
    height: 40px;
    position: relative;
    top: 8px;
  }
}

.PlayerInfo {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(147, 147, 147);
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Roboto", sans-serif;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 70vh;
  margin: 50px 0;
  min-height: 700px;
  opacity: 0.9;
  overflow: auto;
  padding: 0 5%;
  position: relative;
  width: 50vw;
}

.player-info-name {
  font-size: 36px;
}

.player-info-pic {
  height: 250px;
  margin-bottom: 10px;
}

.player-info-teams,
.player-info-totals {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 100px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 5px;
  width: 100%;
  font-size: 28px;
}

.player-info-totals {
  line-height: 1.5;
  min-height: 100px;
  text-align: center;
  width: 80%;
}

.player-info-teams {
  min-height: 60px;
}

.player-info-num {
  font-family: "Permanent Marker", cursive;
  font-size: 50px;
}

.player-info-bonus {
  background: grey;
  margin-bottom: 30px;
  min-height: 100px;
  overflow: auto;
  text-align: center;
  width: 100%;
}

.player-info-bonus-title {
  font-family: "Permanent Marker", cursive;
  font-size: 40px;
  margin: 0;
}

.eliminated {
  color: rgb(160, 0, 0);
  text-decoration: line-through;
}

.back-btn {
  background: rgb(84, 84, 84);
  border: none;
  color: white;
  cursor: pointer;
  font-size: 36px;
  min-height: 50px;
  padding: 10px 20px;
  position: absolute;
  right: 0px;
  transition: all 0.5s ease-out;
}

.back-btn:hover {
  background: rgb(227, 227, 227);
  color: rgb(84, 84, 84);
  transition: all 0.5s ease-out;
}

@media screen and (max-width: 1300px) {
  .player-info-teams,
  .player-info-totals {
    font-size: 22px;
  }
}

@media screen and (max-width: 1000px) {
  .PlayerInfo {
    width: 70vw;
  }

  .player-info-teams {
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 140px;
  }

  .player-info-teams h5 {
    margin: 0;
  }

  .player-info-num {
    font-size: 40px;
  }
}

@media screen and (max-width: 500px) {
  .PlayerInfo {
    padding: 0;
    width: 80vw;
  }

  .player-info-pic {
    height: 250px;
  }

  .player-info-teams,
  .player-info-totals {
    font-size: 18px;
  }

  .player-info-num {
    font-size: 30px;
  }

  .back-btn {
    font-size: 18px;
    margin: 30px;
    min-height: 40px;
    padding: 10px 20px;
    transition: all 0.5s ease-out;
  }
}

.Player {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(147, 147, 147);
  color: white;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  font-family: "Roboto", sans-serif;
  font-size: 30px;
  height: 80px;
  margin: 10px 0;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 20px;
  transition: all 0.5s ease-in-out;
}

.Player:hover {
  background: rgb(84, 84, 84);
  transition: all 0.5s ease-in-out;
}

.player-pic-wrapper {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 80px;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80px;
}

.player-pic {
  max-height: 80px;
  max-width: 80px;
}

.player-name {
  width: 100px;
}

.player-rank {
  width: 60px;
}

@media screen and (max-width: 900px) {
  .Player {
    font-size: 22px;
  }
}

@media screen and (max-width: 760px) {
  .Player {
    font-size: 18px;
  }
}

@media screen and (max-width: 500px) {
  .Player {
    font-size: 16px;
    height: 60px;
  }
  .player-pic {
    height: 60px;
  }
}

.Standings {
  background: silver;
  height: 75vh;
  opacity: 0.9;
  overflow: auto;
  padding: 0 5%;
  width: 60vw;
}

.current-standings-header {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(84, 84, 84);
  color: white;
  display: -webkit-flex;
  display: flex;
  font-size: 22px;
  height: 50px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
  padding-left: 12px;
  padding-right: 32.5%;
}

.current-standings-points {
  position: relative;
  left: 20px;
}

.loading-img {
  height: 200px;
  margin: auto;
}

@media screen and (max-width: 1300px) {
  .current-standings {
    width: 70%;
  }
}

@media screen and (max-width: 760px) {
  .current-standings {
    height: 80vh;
    width: 80%;
  }

  .current-standings-header {
    font-size: 16px;
    padding-right: 30%;
  }

  .current-standings-points {
    left: 10px;
  }
}

@media screen and (max-width: 500px) {
  .Standings {
    height: auto;
    margin: 20px 0;
    width: 80vw;
  }

  .current-standings-points {
    left: 30px;
  }
}

.MainPage {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.App {
  background: url(/long-shot-league/static/media/court.58e6fc11.jpg) center no-repeat;
  background-attachment: fixed;
  background-size: cover;
  display: grid;
  grid-template-rows: 100px 1fr;
  min-height: 100vh;
}

@media screen and (max-width: 760px) {
  .App {
    grid-template-rows: 100px 1fr;
  }
}

